import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import {
  Box,
  Paper,
  Typography,
  Button,
  CircularProgress,
  Divider,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField,
  Alert
} from '@mui/material';
import { resumesApi, jobsApi, type Resume, type Job } from '../services/api';

export const CandidateDetails: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const [resume, setResume] = useState<Resume | null>(null);
  const [job, setJob] = useState<Job | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [status, setStatus] = useState<Resume['status']>('pending');
  const [feedback, setFeedback] = useState('');

  useEffect(() => {
    const fetchData = async () => {
      if (!id) return;
      
      try {
        setLoading(true);
        const resumeResponse = await resumesApi.getById(id);
        const resume = resumeResponse.data;
        setResume(resume);
        setStatus(resume.status);
        setFeedback(resume.feedback || '');

        const jobResponse = await jobsApi.getById(resume.jobId);
        setJob(jobResponse.data);
      } catch (error) {
        console.error('Error fetching candidate details:', error);
        setError('Failed to load candidate details');
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [id]);

  const handleStatusUpdate = async () => {
    if (!id) return;

    try {
      setLoading(true);
      await resumesApi.updateStatus(id, status, feedback);
      setError(null);
    } catch (error) {
      console.error('Error updating status:', error);
      setError('Failed to update candidate status');
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
        <CircularProgress />
      </Box>
    );
  }

  if (error || !resume || !job) {
    return (
      <Box sx={{ p: 3 }}>
        <Alert severity="error">{error || 'Failed to load candidate details'}</Alert>
        <Button sx={{ mt: 2 }} onClick={() => navigate('/dashboard')}>
          Back to Dashboard
        </Button>
      </Box>
    );
  }

  return (
    <Box sx={{ p: 3 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4">Candidate Details</Typography>
        <Button variant="outlined" onClick={() => navigate('/dashboard')}>
          Back to Dashboard
        </Button>
      </Box>

      <Paper sx={{ p: 3, mb: 3 }}>
        <Typography variant="h6" gutterBottom>Basic Information</Typography>
        <Box sx={{ display: 'grid', gridTemplateColumns: 'auto 1fr', gap: 2, alignItems: 'center' }}>
          <Typography variant="subtitle1">Name:</Typography>
          <Typography>{resume.candidateName}</Typography>
          
          <Typography variant="subtitle1">Email:</Typography>
          <Typography>{resume.email}</Typography>
          
          <Typography variant="subtitle1">Applied Job:</Typography>
          <Typography>{job.title}</Typography>
          
          <Typography variant="subtitle1">Application Date:</Typography>
          <Typography>{new Date(resume.createdAt).toLocaleDateString()}</Typography>
          
          <Typography variant="subtitle1">Score:</Typography>
          <Typography color={resume.score >= 70 ? 'success.main' : 'warning.main'}>
            {resume.score}%
          </Typography>
        </Box>
      </Paper>

      <Paper sx={{ p: 3, mb: 3 }}>
        <Typography variant="h6" gutterBottom>Resume Content</Typography>
        <Typography sx={{ whiteSpace: 'pre-wrap', fontFamily: 'monospace' }}>
          {resume.resumeText}
        </Typography>
      </Paper>

      <Paper sx={{ p: 3 }}>
        <Typography variant="h6" gutterBottom>Review</Typography>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
          <FormControl fullWidth>
            <InputLabel>Status</InputLabel>
            <Select
              value={status}
              label="Status"
              onChange={(e) => setStatus(e.target.value as Resume['status'])}
            >
              <MenuItem value="pending">Pending</MenuItem>
              <MenuItem value="reviewed">Reviewed</MenuItem>
              <MenuItem value="qualified">Qualified</MenuItem>
              <MenuItem value="rejected">Rejected</MenuItem>
            </Select>
          </FormControl>

          <TextField
            fullWidth
            multiline
            rows={4}
            label="Feedback"
            value={feedback}
            onChange={(e) => setFeedback(e.target.value)}
          />

          <Button
            variant="contained"
            onClick={handleStatusUpdate}
            disabled={loading}
          >
            Update Status
          </Button>
        </Box>
      </Paper>
    </Box>
  );
}; 